			error = err.message;
		}
		createToast(error,
    {
      type: 'danger',
      showIcon: true,
      position: 'bottom-right',
    });
	}
	

	loadingDeployContract.value = false;
};



const deployContractFromTX = async (
	statePath: string, 
	contractSrcTX: string,
	workspace: Workspace,
	tags: Tags) => {
	let initStateSrc = ``;
	loadingDeployContract.value = true;
	try {
